.profpage {

	&__header {
		padding: 30px 0 15px;
	}
	
	&__title {
		text-align: center;
		color: #2e3192;
		font-size: Arial,sans-serif;
		font-size: 2.3rem;
		font-weight: 300;
		line-height: 1.17;

		@media (max-width: 1200px) {
				font-size: 1.5rem;
		}
	}	
}

.profcards {
	display: flex;
	padding: 15px 0;

	@media (max-width: 1200px) {
		flex-wrap: wrap;
		justify-content: center;
	}
}