.profcard {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 20px;
	color: #ebebec;
	background-color: #ffffff;
	border: 1px solid #f3f5f6;
	transition: all 0.3s 0.01s ease;

	&:hover {
		cursor: pointer;
		box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.3);
	}

	&__imgblock {
		border-bottom: 0.5px solid #ebebec;
		width: 255px;
		height: 125px;
		text-align: center;

		&_img {
			width: 100%;
			height: 100%;
		}
	}

	&__title {
		display:flex;
  	align-items:center;
		min-height: 100px;
		color: #505050;
		font-size: Arial,sans-serif;
		font-size: 1.4rem;
		font-weight: 300;
		line-height: 1.5;
	}
}